<template>
  <ul class='list'>
    <li
      class='item'
      v-for='i of letters'
      :key='i'
      :ref='i'
      @touchstart="onTouchStart"
      @touchmove="onTouchMove"
      @touchend="onTouchEnd"
      @click="onLetterClick"
    >
      {{i}}
    </li>
  </ul>
</template>

<script>
export default {
  updated () {
    this.startY = this.$refs['A'][0].offsetTop
  },
  computed: {
    letters () {
      const letters = []
      for (let i in this.cities) {
        letters.push(i)
      }
      return letters
    }
  },
  data () {
    return {
      timer: null,
      startY: 0,
      touchStatus: false
    }
  },
  methods: {
    onTouchStart () {
      this.touchStatus = true
    },
    onTouchMove (e) {
      if (this.touchStatus) {
        if (this.timer) {
          clearTimeout(this.timer)
        }
        this.timer = setTimeout(() => {
          const touchY = e.touches[0].clientY - 79
          const index = Math.floor((touchY - this.startY) / 20)
          if (index >= 0 && index < this.letters.length) {
            this.$emit('change', this.letters[index])
          }
        }, 16)
      }
    },
    onTouchEnd () {
      this.touchStatus = false
    },
    onLetterClick (e) {
      this.$emit('change', e.target.innerText)
    }
  },
  props: {
    cities: Object
  },
  name: 'CityAlphabet'
}
</script>

<style lang='stylus' scoped>
  @import '~styles/variables.styl'
  .list
    display: flex
    flex-direction: column
    justify-content: center
    position: absolute
    top: 1.58rem
    right: 0
    bottom: 0
    width: .4rem
    .item
      line-height: .4rem
      text-align: center
      color: $bgColor
</style>



