<template>
  <div>
    <div class='banner' @click="onBannerClick">
      <img class='banner-img' :src='bannerImg' />
      <div class='banner-info'>
        <div class='banner-tittle'>
          {{sightName}}
        </div>
        <div class="banner-number">
          <span class='iconfont banner-icon'>&#xe692;</span>
          {{bannerImgs.length}}
        </div>
      </div>
    </div>
    <fade-animation>
      <common-gallary
        :imgs='bannerImgs'
        v-show='showGallary'
        @close='onGallaryClose'
      ></common-gallary>
    </fade-animation>
  </div>
</template>

<script>
import CommonGallary from 'common/gallary/Gallary'
import FadeAnimation from 'common/fade/FadeAnimation'

export default {
  props: {
    sightName: String,
    bannerImg: String,
    bannerImgs: Array
  },
  methods: {
    onGallaryClose () {
      this.showGallary = false
    },
    onBannerClick () {
      this.showGallary = true
    }
  },
  data () {
    return {
      showGallary: false
    }
  },
  components: {
    FadeAnimation,
    CommonGallary
  },
  name: 'DetailBanner'
}
</script>

<style lang='stylus' scoped>
  .banner
    position: relative
    overflow: hidden
    height: 0
    padding-bottom: 55%
    .banner-img
      width: 100%
    .banner-info
      display: flex
      position: absolute
      left: 0
      right: 0
      bottom: 0
      line-height: .6rem
      color: #fff
      background-image: linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, .8))
      .banner-tittle
        flex: 1
        font-size: .32rem
        padding: 0 .2rem
      .banner-number
        height: .32rem
        line-height: .4rem
        margin-top: .14rem
        padding: 0 .4rem
        border-radius: .2rem
        background: rgba(0, 0, 0, .8)
        font-size: .24rem
        .banner-icon
          font-size: .24rem
</style>



