<template>
  <div>
    <city-header></city-header>
    <city-search :cities='cities'></city-search>
    <city-list
      :cities='cities'
      :hot='hotCities'
      :letter='letter'
    ></city-list>
    <city-alphabet
      :cities='cities'
      @change='onLetterChange'
    ></city-alphabet>
  </div>
</template>

<script>
import CityHeader from './components/Header'
import CitySearch from './components/Search'
import CityList from './components/List'
import CityAlphabet from './components/Alphabet'
import axios from 'axios'

export default {
  data () {
    return {
      letter: '',
      cities: {},
      hotCities: []
    }
  },
  methods: {
    onLetterChange (letter) {
      this.letter = letter
    },
    getCityInfo () {
      axios.get('/api/city.json')
        .then(this.onGetCityInfoSucc)
    },
    onGetCityInfoSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.cities = data.cities
        this.hotCities = data.hotCities
      }
    }
  },
  mounted () {
    this.getCityInfo()
  },
  components: {
    CityAlphabet,
    CityList,
    CitySearch,
    CityHeader
  },
  name: 'City'
}
</script>

<style lang='stylus' scoped>
</style>



